#wnpApp {
  color: #fff;
  background: linear-gradient(0deg, #010c12 0%, #033536 50%, #28434a 100%);
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  overflow: hidden;
}

#wnpApp .text-secondary {
  color: #b3bdba !important;
}

.wnpContainer {
  grid-template: "wnpAlbumArt wnpMediaInfo" 1fr
                 "wnpFooter wnpFooter" 110px
                 / 1fr 1fr;
  grid-auto-flow: row;
  gap: 0;
  width: 100%;
  height: 100%;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.wnpAlbumArt {
  text-align: center;
  grid-area: wnpAlbumArt;
  padding: 30px;
  overflow: hidden;
}

.wnpMediaInfo {
  grid-area: wnpMediaInfo;
  padding: 30px 30px 30px 0;
}

.wnpFooter {
  grid-area: wnpFooter;
  padding: 0 30px;
}

#wnpBgAlbumArt {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#bgAlbumArtBlur {
  filter: blur(12px) brightness(.6) opacity(.6);
  background-image: url("fake-album-1.0dd37020.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  transform: scale(1.25);
}

#albumArt {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
  box-shadow: 3px 3px 16px #000000bf;
}

.wnpMediaInfo {
  text-shadow: 3px 3px 8px #000000bf;
}

#mediaSource {
  text-align: center;
}

#mediaSource img {
  filter: invert();
  max-height: 16px;
  margin-bottom: 15px;
}

#mediaQualityIdent.badge-outlined {
  font-weight: var(--bs-body-font-weight);
  border: 1px solid #b3bdba;
  margin-right: 10px;
}

.wnpFooter #progressPercent {
  --bs-bg-opacity: .5;
}

.wnpFooter .btn-group {
  margin-top: 5px;
}

.wnpFooter .btn-group button {
  border: none;
}

.wnpFooter .btn-group button:hover, .wnpFooter .btn-group button:active {
  color: var(--bs-btn-color);
  background-color: #0000;
}

#btnPlay {
  font-size: 2.25rem;
}

#btnPrev, #btnNext {
  font-size: 1.75rem;
}

#devNameHolder {
  position: absolute;
  bottom: 15px;
  left: 30px;
}

#devVolumeHolder {
  text-align: right;
  position: absolute;
  bottom: 15px;
  right: 30px;
}

#wnpSettings {
  border: none;
  position: absolute;
  top: 5px;
  right: 15px;
}

.modal {
  color: #000;
}

@media screen and (width <= 500px) {
  .wnpContainer {
    grid-template: "wnpAlbumArt" 2fr
                   "wnpMediaInfo" 1fr
                   "wnpFooter" 140px
                   / 1fr;
    gap: 0;
    display: grid;
  }

  .wnpMediaInfo {
    padding: 0 30px;
  }
}

